import React from 'react'
import BedsheetData from "./BedsheetData"
import PillowCoverData from "./PillowCoverData"
import SofaCoverData from "./SofaCoverData"
import LadiesDupattaData from "./LadiesDupattaData"
import { RxStarFilled } from "react-icons/rx"
import { Link, useLocation } from "react-router-dom";
import SofaCat from './SofaCat'

const breadcrumbs = [
    { id: 1, name: 'Home', href: '/reactapp/home' },
    { id: 2, name: 'Sofa Cover', href: '/reactapp/home/sofacover' },
];

const Cat = [BedsheetData, PillowCoverData, SofaCoverData, LadiesDupattaData];

export default function Example() {
    const location = useLocation();
    const locOfObj = location.pathname.at(-1);
    const index = Object.values(Cat[2])[locOfObj-1];
    
    return (
        <>
        <div className="bg-white md:mb-5">
            <div className="pt-6">
                <nav aria-label="Breadcrumb">
                    <ol role="list" className="mx-auto flex max-w-2xl items-center space-x-2 px-4 sm:px-6 lg:max-w-7xl lg:px-8">
                        {breadcrumbs.map((breadcrumb) => (
                            <li key={breadcrumb.id}>
                                <div className="flex items-center">
                                    <Link to={breadcrumb.href} className="mr-2 text-sm font-medium text-gray-900 decoration-transparent hover:decoration-transparent duration-500">
                                        {breadcrumb.name}
                                    </Link>
                                    <svg
                                        width={16}
                                        height={20}
                                        viewBox="0 0 16 20"
                                        fill="currentColor"
                                        aria-hidden="true"
                                        className="h-5 w-4 text-gray-300"
                                    >
                                        <path d="M5.697 4.34L8.98 16.532h1.327L7.025 4.341H5.697z" />
                                    </svg>
                                </div>
                            </li>
                        ))}
                        <li className="text-sm">
                            <a href={index.href} aria-current="page" className="font-medium text-gray-500 hover:text-gray-600 decoration-transparent hover:decoration-transparent">
                                {index.name}
                            </a>
                        </li>
                    </ol>
                </nav>

                {/* Image gallery */}
                < div className="mx-auto mt-6 max-w-2xl sm:px-6 lg:grid lg:max-w-7xl lg:grid-cols-2 lg:gap-x-8 lg:px-8" >
                    <div className="md:aspect-h-4 md:aspect-w-3 p-3 rounded-lg overflow-hidden">
                        <img
                            src={index.imageSrc}
                            alt={index.imageAlt}
                            className="h-full w-full object-cover object-center"
                        />
                    </div>

                    {/* Product info */}
                    < div className="md:float-right  max-w-2xl px-4 pb-16 pt-3 sm:px-6 lg:grid lg:max-w-7xl lg:grid-rows-[auto,auto,1fr] lg:gap-x-8 lg:px-8 lg:pb-24 lg:pt-16" >

                        < div className="lg:row-span-3 lg:mt-0" >
                            {/* Name */}
                            <div className="mb-4">
                                <h1 className="text-2xl font-bold tracking-tight text-gray-900 sm:text-3xl">{index.name}</h1>
                            </div>

                            {/* Price */}
                            <div className='mb-4'>
                                <h2 className="text-lg font-medium text-gray-900 pb-1">PRICE</h2>
                                <span className="text-md text-gray-700">{index.price}</span>
                                <span className="text-md italic text-gray-400 pl-3 line-through">{index.orgPrice}</span>
                            </div>

                            {/* Reviews */}
                            <div className="mb-4">
                                <h3 className="text-lg font-medium text-gray-900 pb-1">REVIEWS</h3>
                                <span className="text-md text-yellow-500 inline-block pr-1"><RxStarFilled /></span>
                                <span className='text-md text-gray-700'>{index.rating}</span>
                            </div>

                            {/* Colors */}
                            <div className="mb-4">
                                <h3 className="text-lg font-medium text-gray-900 pb-1">COLOR</h3>
                                <p className='text-md text-gray-700'>{index.color}</p>
                            </div>

                            {/* Sizes */}
                            <div className="mb-4">
                                <h3 className="text-lg font-medium text-gray-900 pb-1">SIZE</h3>
                                <p className='text-md text-gray-700'>{index.size}</p>
                            </div>

                            <div className="flex justify-between">
                                <button type="submit"
                                    className="flex w-50 items-center justify-center rounded-md border-b-4 border-blue-700 bg-indigo-500 px-8 py-3 font-medium text-white hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2 duration-500 mr-3"
                                >
                                    Add To Bag
                                </button>

                                <button type="submit"
                                    className="flex w-50 items-center justify-center rounded-md border-b-4 border-blue-700 px-8 py-3 font-medium text-indigo-700 hover:bg-indigo-700 hover:text-white focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2 duration-500"
                                >
                                    Buy Now
                                </button>
                            </div>

                        </div >
                    </div >
                </div >
            </div >
        </div >
        <SofaCat/>
        </>
    )
}
