import React from 'react';
import {Link} from 'react-router-dom'
import ScrollToTop from "react-scroll-to-top";

const ScrollToTopDesign = {
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  margin: "0px",
  padding: "0px",
  width: "30px",
  borderRadius: "50%"
}

const Footer = () => {
  return (
    <>
    <ScrollToTop smooth style={ScrollToTopDesign} className='animate-bounce' />
    <footer className="footer bg-gray-900" style={{ borderTop: "1px solid grey" }}>
      <div className="container">
        <ul  className='list-none flex justify-between items-center ps-2 pe-2 md:ps-96 md:pe-96 py-3 text-white'>
          <li>
            <Link to="/reactapp/home/bedsheet" className='hover:decoration-transparent duration-300'>Bedsheet</Link>
          </li>
          <li>
            <Link to="/reactapp/home/pillowcover" className='hover:decoration-transparent duration-300'>Pillow Cover</Link>
          </li>
          <li>
            <Link to="/reactapp/home/sofacover" className='hover:decoration-transparent duration-300'>Sofa Cover</Link>
          </li>
          <li>
            <Link to="/reactapp/home/dupatta" className='hover:decoration-transparent duration-300'>Ladies Dupatta</Link>
          </li>
        </ul>
        <p className="text-center text-white p-3">&copy; 2024 JVG Textiles. All rights reserved.</p>
      </div>
    </footer>
    </>
  );
};

export default Footer;