import React from 'react'
// import { MoonLoader } from 'react-spinners';
import PuffLoader from "react-spinners/PuffLoader";

const pos = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%)",
};

function Loader() {
    return (
        <div style={pos}>
            <PuffLoader
                color={"blue"}
                loading={"true"}

                // size={20}
                aria-label="Loading Spinner"
                data-testid="loader"
            />
        </div>
    )
}

export default Loader
