import React from 'react'
import Product from "./Product"
import { Link } from 'react-router-dom';

function productCategories() {
    return (
        <div className="bg-white">
            <div className="mx-auto max-w-2xl px-1 py-5 sm:px-6 sm:py-24 lg:max-w-7xl lg:px-8">
                <h2 className="text-2xl font-bold tracking-tight text-gray-900 pb-1 pl-1">PRODUCT ITEMS</h2>
                <hr style={{width:"120px"}} className="border-indigo-500 border-2 outline-8 ml-1" />

                <div className="mt-6 grid grid-cols-1 gap-x-6 gap-y-10 sm:grid-cols-2 lg:grid-cols-4 xl:gap-x-8">
                    {Product.map((product) => (
                        <div key={product.id} className="group relative">
                            <div className="aspect-h-1 aspect-w-1 w-full overflow-hidden rounded-md bg-gray-200 lg:aspect-none lg:h-80">
                                <img
                                    src={product.imageSrc}
                                    alt={product.imageAlt}
                                    className="h-full w-full object-cover object-center lg:h-full lg:w-full group-hover:scale-110 duration-500"
                                />
                            </div>
                            <div className="mt-4 flex justify-between">
                                <div>
                                    <h3 className="text-gray-700">
                                        <Link to={product.href} className='font-medium hover:decoration-transparent'>
                                            <span aria-hidden="true" className="absolute inset-0" />
                                            {product.name}
                                        </Link>
                                    </h3>
                                    <p className="text-sm italic text-gray-500">{product.price}</p>
                                </div>
                                {/* <p className="text-sm font-medium text-gray-900">{product.price}</p> */}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default productCategories
