import React from 'react'
import { Link } from 'react-router-dom'
import { RxStarFilled } from "react-icons/rx"
import Footer from './Footer'
import BedsheetData from "./BedsheetData"

function BedsheetCat() {
    const changeName = document.querySelector(".changeName");
    function topFunction() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
    }
    return (
        <>
            <div className="bg-white">
                <div className="mx-auto max-w-2xl px-1 py-4 sm:px-6 sm:py-24 lg:max-w-7xl lg:px-8">
                    <h2 className="text-2xl font-bold tracking-tight text-gray-900 pb-1 changeName">BEDSHEETS</h2>
                    <hr style={{ width: "70px" }} className='border-indigo-500 animate-bounce border-2 outline-8' />

                    <div className="mt-6 grid grid-cols-1 gap-x-6 gap-y-10 sm:grid-cols-2 lg:grid-cols-4 xl:gap-x-8">
                        {BedsheetData.map((product) => (
                            <div key={product.id} className="group relative">
                                {/* <div className="aspect-h-1 aspect-w-1 w-full overflow-hidden rounded-t-md bg-gray-200 lg:aspect-none group-hover:opacity-75 lg:h-80"> */}
                                <div className="aspect-h-1 aspect-w-1 w-full overflow-hidden rounded-t-md bg-gray-200 lg:aspect-none lg:h-80">
                                    <img
                                        src={product.imageSrc}
                                        alt={product.imageAlt}
                                        className="h-full w-full object-cover object-center lg:h-full lg:w-full group-hover:scale-110 duration-500"
                                    />
                                </div>
                                <div className="mt-0 rounded-b-md p-2 flex justify-between bg-stone-100">
                                    <div>
                                        <h3 className="text-gray-700 pb-1">
                                            <Link to={product.href} className='font-medium hover:decoration-transparent' onClick={() => { changeName.innerHTML = "RELATED PRODUCTS"; topFunction(); }}>
                                                <span aria-hidden="true" className="absolute inset-0" />
                                                {product.name}
                                            </Link>
                                        </h3>
                                        <p className='text-sm text-gray-700'>{product.color}</p>
                                        {/* <span className="text-2xl text-yellow-500 ">&#9733;</span> */}
                                        <p className='flex justify-left align-center pt-1'>
                                            <span className="text-lg text-yellow-500 inline-block pr-1"><RxStarFilled /></span>
                                            <span className="text-sm font-small text-gray-400 "> {product.rating}</span>
                                        </p>
                                        <span className="text-sm font-medium italic">{product.price}</span>
                                        <del className="text-sm font-medium italic text-gray-400 pl-2">{product.orgPrice}</del>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
            <Footer />
        </>
    )
}

export default BedsheetCat 
